[data-copy]::before {
  position: absolute;
  top: 32px;
  right: 0;
  left: 0;
  z-index: 1;
  display: inline-block;
  width: fit-content;
  margin: auto;
  padding: 4px 8px;
  font-size: 12px;
  border: 1px solid;
  border-radius: var(--border-radius-base);
  transition: background-color 0.2s, color 0.2s, border-color 0.2s;
  animation-fill-mode: forwards;
  content: '\590d\5236\6210\529f';
}
[data-copy='success']::before {
  color: var(--success-color);
  background-color: var(--success-color-deprecated-bg);
  border-color: var(--success-color-deprecated-border);
  animation: copy-slide-in 0.3s ease-in-out;
  content: '\590d\5236\6210\529f';
}
[data-copy='failure']::before {
  color: var(--error-color);
  background-color: var(--error-color-deprecated-bg);
  border-color: var(--error-color-deprecated-border);
  animation: copy-slide-in 0.3s ease-in-out, error 1s ease-in-out 1s;
  content: '\590d\5236\5931\8d25';
}
[data-copy-exit]::before {
  animation: copy-slide-out 0.3s forwards ease-in-out;
}
@keyframes copy-slide-in {
  from {
    transform: translate3d(0, -100%, 0) scale(0);
    opacity: 0;
  }
  to {
    transform: translate3d(0, 0, 0) scale(1);
    opacity: 1;
  }
}
@keyframes copy-slide-out {
  from {
    transform: translate3d(0, 0, 0) scale(1);
    opacity: 1;
  }
  to {
    transform: translate3d(0, -100%, 0) scale(0);
    opacity: 0;
  }
}
.markdown-box {
  max-width: 1000px;
  margin: 0 auto 70px;
  overflow-wrap: break-word;
}
.markdown-box .markdown-box {
  margin: 0;
}
.markdown-box [data-prefix]::before {
  color: var(--primary-color);
  content: attr(data-prefix);
}
.markdown-box ol {
  margin: 0;
  padding: 0 0 0 24px;
}
.markdown-box > .markdown-toc {
  position: absolute;
  top: 116px;
  right: 0;
  z-index: 9;
  max-width: 200px;
  height: fit-content;
  max-height: 75%;
  margin: auto;
  padding: 16px 12px 16px 24px;
  overflow-y: auto;
  background-color: var(--toc-bg, rgba(255, 255, 255, 0.8));
  backdrop-filter: blur(16px);
  border-left: 10px solid var(--primary-color-deprecated-border, #91d5ff);
  border-radius: var(--border-radius-base, 4px);
  box-shadow: -10px 0 10px -5px var(--text-shadow-color, rgba(0, 0, 0, 0.1));
  transform: translateX(0) translateZ(0);
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: border-color, background-color, box-shadow, transform;
}
.markdown-box > .markdown-toc li {
  list-style: circle;
}
.markdown-box > .markdown-toc li a {
  max-width: 100%;
  font-size: 12px;
  text-decoration: none;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: color;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
  float: left;
}
.markdown-box > .markdown-toc ol {
  padding: 0 0 0 28px;
}
.markdown-box > .markdown-toc ol li {
  list-style: cjk-ideographic;
}
.markdown-box > .markdown-toc ol ol {
  padding: 0 0 0 14px;
}
.markdown-box > .markdown-toc ol ol li {
  list-style: decimal;
}
.markdown-box > .markdown-toc ol ol ol li {
  list-style: circle;
}
.markdown-box > .markdown-toc .active {
  list-style: outside;
  color: var(--primary-color, rgba(0, 0, 0, 0.65));
}
.markdown-box > .markdown-toc .active a {
  color: var(--primary-color, rgba(0, 0, 0, 0.65));
}
.markdown-box p > code,
.markdown-box h1 > code,
.markdown-box h2 > code,
.markdown-box h3 > code,
.markdown-box h4 > code,
.markdown-box h5 > code,
.markdown-box h6 > code,
.markdown-box li > code,
.markdown-box mark {
  padding: 2px 4px;
  color: var(--md-mark-color, #e9887c);
  font-size: 90%;
  background-color: var(--mark-bg, #f9f2f4);
  border-radius: var(--border-radius-base, 4px);
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: background-color, color;
}
.markdown-box a {
  position: relative;
  color: var(--text-color, rgba(0, 0, 0, 0.65));
  text-decoration: none;
}
.markdown-box a::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--primary-color-hover);
  transform: scaleX(0);
  transform-origin: center;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: transform, background-color;
  content: '';
}
.markdown-box a:visited {
  color: var(--text-color-secondary, rgba(0, 0, 0, 0.45));
}
.markdown-box a:hover {
  color: var(--primary-color-hover);
}
.markdown-box a:hover::after {
  transform: scaleX(1);
}
.markdown-box h1,
.markdown-box h2,
.markdown-box h3,
.markdown-box h4,
