  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
}
.markdown-box blockquote {
  position: relative;
  margin: 30px 60px;
  padding: 16px;
  font-weight: 500;
  background-color: var(--blockquote-bg, #ecf8ff);
  border-radius: 8px;
  transition-property: background-color;
}
.markdown-box blockquote::before,
.markdown-box blockquote::after {
  position: absolute;
  color: var(--blockquote-icon-color, #fb5);
  font-size: 48px;
  font-family: 'neko__icon', sans-serif;
  transition-property: color;
}
.markdown-box blockquote::before {
  top: -8px;
  left: 0;
  transform: translateX(-53px);
  content: '\e68e';
}
.markdown-box blockquote::after {
  right: 0;
  bottom: -8px;
  transform: translateX(53px);
  content: '\e68d';
}
.markdown-box blockquote cite {
  color: var(--cite-color, #bfbfbf);
  font-size: var(--font-size-base, 14px);
  line-height: 20px;
}
.markdown-box blockquote cite::before {
  content: '\2014 \00A0';
}
.markdown-box blockquote p {
  margin: auto 0;
  color: var(--blockquote-color, #5e6d82);
  font-size: var(--font-size-base, 14px);
  line-height: 24px;
  transition-property: color;
}
.markdown-box p {
  line-height: 1.8;
