.header {
  width: calc(100% - 346px);
  height: 75px;
  display: flex;
  align-items: center;
  padding: 0 24px;
  position: fixed;
  top: 16px;
  right: 16px;
  background-color: var(--header-bg, rgba(255, 255, 255, 0.9));
  backdrop-filter: blur(16px);
  border-radius: 4px 4px 0 0;
  z-index: 2;
  box-shadow: 0 1px 1px 0 var(--header-shadow, rgba(0, 0, 0, 0.05));
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: box-shadow, background-color;
}
.left {
  display: flex;
  align-items: flex-end;
  flex: 1;
}
.left h2 {
  margin: 0 16px;
}
.left .sub-title {
  opacity: 0.7;
}
.switch-theme {
  font-size: 24px;
  padding: 4px;
  cursor: pointer;
}
