.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  padding: 24px;
  color: var(--disabled-color, rgba(255, 255, 255, 0.85));
  background-color: var(--footer-bg, #333);
  margin: 0 -24px -24px;
  height: 75px;
  text-align: center;
  z-index: 2;
  box-shadow: 0 1px 1px 0 var(--header-shadow, rgba(0, 0, 0, 0.05));
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: box-shadow, background-color;
}
.footer a {
  color: var(--disabled-color);
}
.beian {
  padding: 0 12px;
}
[data-theme='dark'] {
  --footer-bg: #141414;
}
