@charset 'utf-8';
code[class*='language-'],
pre[class*='language-'] {
  color: var(--code-color, #90a4ae);
}
code[class*='language-']::selection,
pre[class*='language-']::selection,
code[class*='language-'] ::selection,
pre[class*='language-'] ::selection {
  text-shadow: 0 1px var(--text-shadow-color);
  background-color: var(--text-selection-bg);
}
.language-css > code,
.language-sass > code,
.language-scss > code {
  color: var(--code-lang-style-color, #f76d47);
}
[class*='language-'] .namespace {
  opacity: 0.7;
}
