}
.token.variable {
  color: var(--token-variable, #e53935);
}
.treeview-part .entry-line {
  position: relative;
  display: inline-block;
  width: 1.2em;
  text-indent: -99em;
  vertical-align: top;
}
.treeview-part .entry-line::before,
.treeview-part .line-h::after {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  content: '';
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: border-color;
}
.treeview-part .line-h::before,
.treeview-part .line-v::before,
.treeview-part .line-v-last::before {
  border-left: 1px solid var(--border-color-base);
}
.treeview-part .line-v-last::before,
.treeview-part .line-h::after {
  height: 50%;
  border-bottom: 1px solid var(--border-color-base);
}
.treeview-part .entry-name {
  position: relative;
  display: inline-block;
  vertical-align: top;
}
.treeview-part .entry-name.dotfile {
  opacity: 0.5;
}
.treeview-part .entry-name::before {
  display: block;
  float: left;
  width: 2.5ex;
  color: #9e9e9e;
  font-size: inherit;
  font-family: PrismTreeview, sans-serif;
  font-style: normal;
  content: '\ea01';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.treeview-part .entry-name.dir::before {
  color: orange;
  content: '\ea02';
}
