code[class*='language-'],
pre {
  font-family: 'Liberation Mono', Monaco, Menlo, Consolas, 'Source Code Pro', 'Ubuntu Mono', 'Microsoft Yahei', '微软雅黑', Courier, 'Helvetica Neue', 'Lantinghei SC', STXihei, WenQuanYi, sans-serif;
  white-space: pre;
  text-align: left;
  word-wrap: normal;
  word-break: normal;
  word-spacing: normal;
  tab-size: 4;
  hyphens: none;
}
code[class*='language-'] a[href],
pre a[href] {
  cursor: help;
}
/* diff-highlight */
pre[class*='language-diff'] > code .token.deleted:not(.prefix),
pre > code[class*='language-diff'] .token.deleted:not(.prefix) {
  display: block;
  color: inherit;
  background-color: rgba(255, 0, 0, 0.1);
}
pre[class*='language-diff'] > code .token.inserted:not(.prefix),
pre > code[class*='language-diff'] .token.inserted:not(.prefix) {
  display: block;
  color: inherit;
  background-color: rgba(0, 255, 128, 0.1);
}
/* inline-color */
span .inline-color-wrapper {
  display: inline-block;
  box-sizing: border-box;
  width: 1.333ch;
  height: 1.333ch;
  margin: 0 0.333ch;
  overflow: hidden;
  background: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAyIDIiPjxwYXRoIGZpbGw9ImdyYXkiIGQ9Ik0wIDBoMnYySDB6Ii8+PHBhdGggZmlsbD0id2hpdGUiIGQ9Ik0wIDBoMXYxSDB6TTEgMWgxdjFIMXoiLz48L3N2Zz4=');
  background-position: center;
  background-size: 110%;
  border: 1px solid #fff;
  outline: 1px solid rgba(0, 0, 0, 0.5);
}
span .inline-color {
  display: block;
  width: 120%;
  height: 120%;
}
pre {
  position: relative;
  display: block;
  max-width: 100%;
  margin: 35px auto;
  overflow: visible;
  font-size: var(--font-size-base);
  line-height: 20px;
  background-color: var(--code-bg, #fff);
  border-radius: var(--border-radius-base, 5px);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition-property: color, background-color, box-shadow;
}
pre .toolbar::after {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;
  width: 100%;
  height: 24px;
  overflow: hidden;
  color: var(--text-color, #4d4d4c);
  font-weight: bold;
  font-size: 14px;
  font-family: Ubuntu, sans-serif;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  background-color: var(--code-toolbar-bg, rgba(220, 224, 229, 0.5));
  border-radius: 5px 5px 0 0;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(16px);
  content: attr(data-lang) '';
  transition-property: background-color, border-color, color;
}
pre .toolbar::before {
  position: absolute;
  top: 10px;
  left: 12px;
  z-index: 3;
  display: inline-block;
  width: 4px;
  height: 4px;
  text-align: center;
  background-color: #444;
  border-radius: 4px;
  box-shadow: 0 0 0 4px #999, 20px 0 0 4px #999, 40px 0 0 4px #999;
  cursor: pointer;
  content: '';
}
pre .toolbar .toolbar-copy {
  position: absolute;
  top: 0;
  right: 8px;
  z-index: 3;
  padding: 0;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}
pre .toolbar .toolbar-copy::after {
  display: block;
  color: var(--text-color, #4d4d4c);
  font-weight: 600;
  font-size: 14px;
  font-family: 'neko__icon', sans-serif;
  line-height: 24px;
  text-transform: uppercase;
  content: '\e631';
  transition-property: color;
}
pre .toolbar .toolbar-copy:hover::after {
  color: var(--primary-color, #1c5f84);
  font-weight: bold;
}
pre > code {
  font-size: var(--font-size-base);
  display: block;
  max-width: 100%;
  margin: 0 16px 8px;
  padding: 32px 0 8px;
  overflow: auto;
  color: #585260;
  line-height: 1.8;
  white-space: pre;
  pointer-events: auto;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: box-shadow, background-color, border-color, color;
}
pre.line-numbers > code {
  margin: 0 10px 0 54px;
  white-space: inherit;
  counter-reset: linenumber;
}
pre.line-numbers .line-numbers-rows {
  position: absolute;
  top: 0;
  left: 0;
  width: 44px;
  max-height: inherit;
  padding: 32px 0 8px;
  overflow-y: auto;
  font-size: 100%;
  letter-spacing: -1px;
  background-color: var(--code-bg, #fff);
  border-right: 1px solid var(--border-color-base);
  border-radius: 5px 0 0 5px;
  user-select: none;
  pointer-events: none;
  transition-property: background-color, border-color;
}
pre.line-numbers .line-numbers-rows > span {
  display: block;
  counter-increment: linenumber;
}
pre.line-numbers .line-numbers-rows > span::before {
  display: block;
  padding-right: 0.8em;
  color: #999;
  text-align: right;
  opacity: 0.5;
  content: counter(linenumber);
}
pre::-webkit-scrollbar,
pre ::-webkit-scrollbar {
  display: none;
}
pre:hover {
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
}
pre:hover .toolbar::before {
  background-color: #fc625d;
  box-shadow: 0 0 0 4px #fc625d, 20px 0 0 4px #fdbc40, 40px 0 0 4px #35cd4b;
}
pre,
.line-numbers-rows,
.toolbar::after,
.toolbar-copy::after {
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
}
@media print {
  code[class*='language-'],
  pre[class*='language-'] {
    text-shadow: none;
  }
}
