@charset 'utf-8';
.tag {
  position: relative;
  border-radius: 4px 4px 4px 0;
  color: #fff;
  padding: 2px 6px;
  font-size: 10px;
  line-height: 16px;
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.4), transparent) var(--tag-bg, #ea3447);
  background-blend-mode: soft-light;
}
.tag::before {
  content: '';
  position: absolute;
  width: 3px;
  height: 3px;
  left: 0;
  bottom: -3px;
  background-color: inherit;
  filter: brightness(0.7);
  clip-path: polygon(0 0, 100% 0, 100% 100%);
}
