.snapshot {
  border: 1px solid var(--border-color-base, #d9d9d9);
  border-radius: var(--border-radius-base, 4px);
  margin-bottom: 24px;
}
.snapshot:last-child {
  margin-bottom: 0;
}
.preview {
  padding: 24px;
  text-align: right;
  max-width: 100%;
  box-shadow: 0 1px 0 var(--border-color-base, #d9d9d9);
  background-color: var(--component-background);
}
.preview .view {
  text-align: left;
}
.toolbar {
  width: fit-content;
  background-color: rgba(221, 221, 221, 0.5);
  padding: 0 16px;
  text-align: center;
  cursor: pointer;
  float: right;
  margin-left: 8px;
  border-radius: 4px 4px 0 0;
  user-select: none;
}
.toolbar:hover,
.toolbar.open {
  color: #1890ff;
  background-color: var(--primary-color-deprecated-bg);
}
.code {
  padding: 24px;
  background-color: var(--body-background);
}
.code :global(.markdown-box),
.code pre {
  margin: 0;
}
.snapshot,
.preview,
.toolbar,
.code {
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: border-color, background-color, box-shadow;
}
