.nav {
  box-sizing: border-box;
  color: var(--text-color);
  width: 250px;
  min-width: 250px;
  background-color: var(--component-background, #fff);
  border-radius: var(--border-radius-base);
  display: flex;
  flex-direction: column;
}
.nav p,
.nav h2 {
  margin-bottom: 0;
  margin-top: 0;
}
.logo {
  display: flex;
  padding: 19.2px 24px;
}
.logo-img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 4px solid transparent;
  overflow: hidden;
  background: url('/Users/moneko/Documents/Develop.nosync/GitHub/neko-ui/node_modules/plugin-runtime/template/favicon.ico') no-repeat center center / contain;
  cursor: pointer;
}
.logo-con {
  margin-left: 16px;
  flex: 1;
}
.logo-con h2 {
  font-weight: 400;
  text-shadow: 2px 2px 2px var(--text-shadow-color, rgba(0, 0, 0, 0.1));
}
.title {
  font-size: var(--font-size-base);
  color: var(--heading-color);
  padding: 0 24px 8px;
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: var(--component-background, #fff);
}
.title::after {
  content: '';
  display: block;
  height: 1px;
  margin: 8px 0 0;
  background-color: var(--border-color-base, #d9d9d9);
  transition-property: background-color;
}
.sider {
  width: 100%;
  flex: 1;
  overflow-y: scroll;
}
.menu {
  margin-bottom: 16px;
  padding: 0 24px;
}
.item {
  min-height: 45px;
  display: flex;
  align-items: center;
  position: relative;
  flex-wrap: wrap;
  border-radius: 4px;
  margin-bottom: 8px;
  cursor: pointer;
  transition-property: background-color;
}
.item::after {
  content: '';
  width: 3px;
  height: 100%;
  background-color: var(--primary-color);
  position: absolute;
  left: -24px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  transform: scale(0);
  transition-property: background-color, transform;
}
.item:hover,
.item.active {
  color: var(--primary-color);
}
.item.active {
  background-color: var(--primary-color-deprecated-bg);
}
.item.active::after {
  transform: scale(1);
}
.icon {
  width: 32px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.con,
.sub-title {
  width: calc(100% - 32px);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.con {
  overflow: hidden;
  position: relative;
}
.sub-title {
  opacity: 0.67;
  margin-left: 32px;
  font-size: 12px;
}
.nav,
.title {
  transition-property: background-color, color;
}
.nav,
.item,
.item::after,
.title::after,
.title {
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
}
