.coverage {
  display: flex;
  padding: 2px;
  max-width: 1000px;
  width: 100%;
  flex-wrap: wrap;
  margin: auto;
}
.item {
  display: flex;
  margin-right: 16px;
  margin-left: 0;
  margin-bottom: 16px;
  border-radius: 4px;
}
.label,
.value {
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.label {
  font-weight: 500;
  color: #fff;
  font-size: 16px;
  text-shadow: 2px 2px 2px var(--text-shadow-color);
}
.value {
  flex-direction: column;
  min-width: 86px;
  text-align: center;
}
.value > div:first-child {
  width: 100%;
  font-weight: 500;
}
.label,
.value,
.value > div,
.item {
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
}
.label,
.value,
.item {
  transition-property: box-shadow, background-color, border-color, color;
}
.success {
  box-shadow: 0 0 2px 0 var(--success-color), inset 0 0 2px 0 var(--success-color);
  border: 1px solid var(--success-color);
}
.success .label {
  background-color: var(--success-color);
  border-right: 1.5px solid var(--success-color);
}
.success .value {
  color: var(--success-color);
}
.success .value > div:first-child {
  border-bottom: 1px solid var(--success-color);
}
.success .value > div {
  transition-property: box-shadow, background-color, border-color;
}
.warning {
  box-shadow: 0 0 2px 0 var(--warning-color), inset 0 0 2px 0 var(--warning-color);
  border: 1px solid var(--warning-color);
}
.warning .label {
  background-color: var(--warning-color);
  border-right: 1.5px solid var(--warning-color);
}
.warning .value {
  color: var(--warning-color);
}
.warning .value > div:first-child {
  border-bottom: 1px solid var(--warning-color);
}
.warning .value > div {
  transition-property: box-shadow, background-color, border-color;
}
.error {
  box-shadow: 0 0 2px 0 var(--error-color), inset 0 0 2px 0 var(--error-color);
  border: 1px solid var(--error-color);
}
.error .label {
  background-color: var(--error-color);
  border-right: 1.5px solid var(--error-color);
}
.error .value {
  color: var(--error-color);
}
.error .value > div:first-child {
  border-bottom: 1px solid var(--error-color);
}
.error .value > div {
  transition-property: box-shadow, background-color, border-color;
}
