.layout {
  display: flex;
  max-height: calc(100vh - 32px);
  padding: 16px;
  height: 100%;
}
.container,
.container > main,
.empty {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.container {
  position: relative;
}
.container > main {
  overflow-y: auto;
}
.readme {
  padding: 100px 24px 24px;
  margin-left: 16px;
  z-index: 1;
  flex: 1;
  background-color: var(--readme-bg, #fff);
  border-radius: 4px;
  overflow-x: hidden;
  transition-property: background-color;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
}
 > header,
 > footer {
  z-index: 2;
}
[data-theme='dark'] {
  --readme-bg: #171717;
}
